import {
    Component, HostBinding, Input, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation,
} from '@angular/core';
import {
    merge, Subject, take, takeUntil, tap, firstValueFrom,
} from 'rxjs';
import { FeaturesService } from 'app/services/features';
import { ModalService } from 'app/utils/modals/modal.service';
import { I360Config } from 'app/utils/config/config';
import { FeatureStatus, FeatureStatusDict } from '@imunify360-api/features';
import { Panel } from 'app/utils/panel';


const TITLE_OF_BUTTONS = {
    [FeatureStatus.installed]: {
        title: 'settings.feature.titles.remove',
    },
    [FeatureStatus.installing]: {
        description: 'settings.feature.titles.installing',
    },
    [FeatureStatus.not_installed]: {
        title: 'settings.feature.titles.install',
    },
    [FeatureStatus.removing]: {
        description: 'settings.feature.titles.uninstalling',
    },
};

@Component({
    selector: 'i360-feature',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './feature.component.html',
    styleUrls: ['./feature.component.scss'],
})
export class FeatureComponent implements OnInit, OnDestroy {
    @ViewChild('modalContent', {static: true}) modal: TemplateRef<any>;
    @HostBinding('class') hostClass = 'config_item';
    FeatureStatus = FeatureStatus;

    @Input() name: string;
    @Input() label: string;
    @Input() displayLog: boolean;
    status: FeatureStatusDict;
    public titleOfButton: string = '';
    public descriptionOfButton: string = '';
    public logFile: string | null;
    get isInstalled() {
        return this.status.status === FeatureStatus.installed
            || this.status.status === FeatureStatus.removing;
    }
    private destroyed = new Subject<void>();
    private buttonClicked = new Subject<void>();
    private getStatus = this.featuresService.getStatus(() => this.name).pipe(
        tap(status => {
            this.setStatus(status);
            if (!this.logFile && this.displayLog && this.isActionProceed()) {
                this.performInstallRemoveAction();
            }
        }),
        takeUntil(merge(this.destroyed, this.buttonClicked)),
    );

    setStatus(val: FeatureStatusDict) {
        this.status = val;
        this.defineMetaData();
    }

    constructor(
        private config: I360Config,
        private featuresService: FeaturesService,
        private modalService: ModalService,
        public panel: Panel,
    ) {
    }


    defineMetaData() {
        const data = TITLE_OF_BUTTONS[this.status.status];
        if (data) {
            this.titleOfButton = data.title;
            this.descriptionOfButton = data.description;
        } else {
            this.titleOfButton = '';
            this.descriptionOfButton = '';
        }
    }

    public isActionProceed(): boolean {
        return this.featuresService.isStatusActive(this.status.status);
    }

    public async onClick() {

        if (false === await this.modalService.open(this.modal)) {
            return;
        }
        this.logFile = null;
        await this.performInstallRemoveAction();
        // need to complete getStatus before creating it again, since for some cases
        // getStatus never completes itself
        // for cases like with kernelcare it completes itself
        this.buttonClicked.next();
        this.getStatus.subscribe();
    }

    ngOnInit() {
        // first status is 'detecting', and need to wait until config is loaded before do requests
        this.getStatus.pipe(take(1)).subscribe();
        this.config.changes.pipe(
            take(1),
            takeUntil(this.destroyed),
        ).subscribe(() => {
            this.getStatus.subscribe();
        });
    }

    ngOnDestroy() {
        this.destroyed.next();
    }

    private async performInstallRemoveAction() {
        try {
            const action = this.isInstalled ? 'remove' : 'install';
            this.setStatus({status: this.isInstalled
                    ? FeatureStatus.removing : FeatureStatus.installing});
            const res = await firstValueFrom(
                this.featuresService.action({name: this.name, action}),
                { defaultValue: { data: { items: null } }}
            );
            this.logFile = res.data.items;
        } catch (e) {
            this.featuresService.handleError(e);
        }
    }
}
